:import('Styles/palette.css') {
  i__const_darkColor_72: darkColor;
  i__const_secondaryColor_73: secondaryColor;
  i__const_highlightColor_74: highlightColor;
  i__const_neutralColorMedium_75: neutralColorMedium;
  i__const_whiteColor_76: whiteColor;
  i__const_pinkRed_77: pinkRed;
}

:export {
  colors: 'Styles/palette.css';
  darkColor: i__const_darkColor_72;
  secondaryColor: i__const_secondaryColor_73;
  highlightColor: i__const_highlightColor_74;
  neutralColorMedium: i__const_neutralColorMedium_75;
  whiteColor: i__const_whiteColor_76;
  pinkRed: i__const_pinkRed_77;
}

.title, .subtitle, .header, .p {
  margin: 0;
}

.p {
  color: i__const_darkColor_72;
}

.label {
  opacity: 0.5;
  font-size: 14px;
  margin-bottom: 2px;
}

.highlight {
  color: i__const_highlightColor_74;
}

.secondary {
  color: i__const_secondaryColor_73;
}

.disabled {
  color: i__const_neutralColorMedium_75;
  cursor: not-allowed;
}

.inverted {
  color: i__const_whiteColor_76;
}

.smallText {
  font-size: 13px;
}

.lead {
  font-size: 18px;
}

.normalText {
  font-weight: normal;
  font-family: 'Lato-Regular';
}

.normalFont {
  font-weight: normal;
  font-family: 'FaktPro-Normal';
}

.bold {
  font-weight: bold;
  font-family: 'FaktPro-Bold';
}

.light {
  font-weight: 300;
  font-family: 'FaktPro-Thin';
}

.error {
  color: i__const_pinkRed_77;
}

a {
  font-family: 'FaktPro-Normal';
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'FaktPro-Normal';
}



