:import('Styles/palette.css') {
  i__const_successAccent_94: successAccent;
  i__const_successNeutral_95: successNeutral;
  i__const_successLight_96: successLight;
  i__const_infoAccent_97: infoAccent;
  i__const_infoNeutral_98: infoNeutral;
  i__const_infoLight_99: infoLight;
  i__const_warningAccent_100: warningAccent;
  i__const_warningNeutral_101: warningNeutral;
  i__const_warningLight_102: warningLight;
  i__const_dangerAccent_103: dangerAccent;
  i__const_dangerNeutral_104: dangerNeutral;
  i__const_dangerLight_105: dangerLight;
  i__const_neutralColorMedium_106: neutralColorMedium;
  i__const_neutralColorLight_107: neutralColorLight;
}


:export {
  successAccent: i__const_successAccent_94;
  successNeutral: i__const_successNeutral_95;
  successLight: i__const_successLight_96;
  infoAccent: i__const_infoAccent_97;
  infoNeutral: i__const_infoNeutral_98;
  infoLight: i__const_infoLight_99;
  warningAccent: i__const_warningAccent_100;
  warningNeutral: i__const_warningNeutral_101;
  warningLight: i__const_warningLight_102;
  dangerAccent: i__const_dangerAccent_103;
  dangerNeutral: i__const_dangerNeutral_104;
  dangerLight: i__const_dangerLight_105;
  neutralColorMedium: i__const_neutralColorMedium_106;
  neutralColorLight: i__const_neutralColorLight_107;
}


.notification {
  padding: 20px 20px 5px 20px;
  background-color: i__const_neutralColorLight_107;
  border: 1px solid;
  border-color: i__const_neutralColorMedium_106;
  border-radius: 2px;
}


.footer{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}

.action{
  padding: 5px;
  cursor: pointer;
}

.success{
  color: i__const_successAccent_94;
  background-color: i__const_successNeutral_95;
  border-color: i__const_successLight_96;
}

.info{
  color: i__const_infoAccent_97;
  background-color: i__const_infoNeutral_98;
  border-color: i__const_infoLight_99;
}

.warning{
  color: i__const_warningAccent_100;
  background-color: i__const_warningNeutral_101;
  border-color: i__const_warningLight_102;
}

.danger{
  color: i__const_dangerAccent_103;
  background-color: i__const_dangerNeutral_104;
  border-color: i__const_dangerLight_105;
}


